import styled, {css} from 'styled-components';

interface ContainerProps{
    focado: boolean;
}

export const Container = styled.div<ContainerProps>`

background: #2f363d;
border: 1px solid #2f363d;
padding: 20px;
border-radius: 20px;
display: flex;
justify-content:space-between;
align-items:center;

${(props) =>
        props.focado &&
        css`
           border-color: #0066ff;
`}

input{
    background: #2f363d;
    border: 0;
    width: 100%;
    color: #fff;
    font-size: 22px;
    margin-left: 10px;
    margin-right: 10px;

    &::placeholder{
            color: #959da5;
        }
}

`;