import React from 'react';
import ModuloProps from '../../interfaces/Modulo';

import { Container } from './styles';

const Modulo: React.FC<ModuloProps> = ({ codigo, descricao }) => {
    return (
        <Container>
            <p>{codigo}</p>
            <p>{descricao}</p>
        </Container>
    );
}

export default Modulo;