import React, { useState, FormEvent } from 'react';
import { Container, Resultado } from './styles';
import Input from '../../components/Input';
import Modulo from '../../components/Modulo';
import Aviso from '../../components/Aviso';
import SemResultado from '../../components/SemResultado';

import ModuloProps from '../../interfaces/Modulo';

import ConsultaModulos from '../../services/ConsultaModulos';

const Consulta: React.FC = () => {

    const [apelido, setApelido] = useState('');
    const [modulos, setModulos] = useState<ModuloProps[]>([]);
    const [mensagem, setMensagem] = useState('');
    const [buscando, setBuscando] = useState(false);

    const emSubmit = async (evento: FormEvent<HTMLFormElement>): Promise<void> => {
        evento.preventDefault();
        if (!!!apelido) {
            alert('Digite o apelido antes');
            return;
        }

        setModulos([]);
        setBuscando(true);

        try {

            const resultado = await ConsultaModulos(apelido);
            setModulos(resultado);

            setMensagem('');

        } catch (erro) {
            setMensagem(`Houve um erro na consulta: ${erro.message}`);
        }

        setBuscando(false);

    }

    return (
        <Container>
            <form onSubmit={emSubmit}>
                <Input
                    value={apelido}
                    onChange={e => setApelido(e.target.value)}
                    placeholder="Digite o apelido do cliente" />
            </form>
            <Resultado>
                {mensagem &&
                    <Aviso mensagem={mensagem} />}
                {modulos.length > 0 &&
                    <Aviso mensagem={'Resultado da pesquisa:'} />}
                {modulos.length > 0 &&
                    modulos.map(modulo => (
                        <Modulo codigo={modulo.codigo} descricao={modulo.descricao} />
                    ))}
                {(modulos.length === 0 && !buscando) &&
                    <SemResultado />}
                {buscando &&
                    <Aviso mensagem="Buscando..."/>}
            </Resultado>
        </Container >
    )
}

export default Consulta;