import styled from 'styled-components';

export const Container = styled.div`

margin-bottom: 30px;

a{
    font-size: 30px;
    color: #fff;
    text-decoration: none;
}

@media (max-width: 700px){
    display:flex;
    justify-content:center;
    align-items:center;
    a{
        font-size: 50px;
    }
}

`;