import API from './API';

import ModuloProps from '../interfaces/Modulo';

const ConsultaModulos = async (apelido: string): Promise<ModuloProps[]> => {

    const response = await API.get<ModuloProps[]>(`PermissaoSistemas/ObterSistemasAutorizados/${apelido}`);
    return response.data;
}

export default ConsultaModulos;