import React from 'react';
import { useHistory } from 'react-router-dom';

import { Container, AreaOpcoes, Opcao } from './styles';

const Home: React.FC = () => {

    const history = useHistory();

    const NavegarParaHsitorico = (): void => {
        history.push('Historico');
    }

    const NavegarParaNovaConculta = (): void => {
        history.push('Consulta');
    }

    const NavegarPeraPerfilCliente = (): void => {
        history.push('Perfil');
    }
    
    return (
        <Container>
            <p>O que deseja consultar?</p>
            <AreaOpcoes>
                <Opcao onClick={NavegarParaNovaConculta}>
                    Nova consulta
                </Opcao>
                <Opcao onClick={NavegarParaHsitorico}>
                    Histórico de consultas
                </Opcao>
                <Opcao onClick={NavegarPeraPerfilCliente}>
                    Perfil do cliente
                </Opcao>
            </AreaOpcoes>
        </Container>
    )
}

export default Home;